import { Select } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { LogCliService } from "../../../services/adminService";
import { MyBox } from "../../core/MyBox";
import { MyButton } from "../../core/MyButton";
import './logCli.css';

export default function ManageLogCliIndexComponent() {
    const [isLoading, setIsLoading] = useState(false);
    const [options, setOptions] = useState([]);
    const [dataSource, setDataSource] = useState('');

    const loadData = useCallback(() => {
        setIsLoading(true);
        LogCliService.GetList()
            .then((rs: any) => {
                if (rs.isSuccess) {
                    setOptions(rs.data.map((data: any, index: number) => {
                        return {
                            label: data,
                            value: data,
                            key: index
                        }
                    }));
                }
            })
            .finally(() => setIsLoading(false));
    }, []);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleChangeOption(data: string) {
        setIsLoading(true);
        LogCliService.GetDetail(data)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    setDataSource(rs.data);
                }
            })
            .finally(() => setIsLoading(false));
    }

    return (
        <MyBox
            title="Log hệ thống"
            isLoading={isLoading}
            buttonList={() => (
                <div>
                    <Link to="/admin/logAction"> <MyButton type="primary" value="Log action" /></Link>
                    <Link to="/admin/chart"> <MyButton type="secondary" value="Chart" /></Link>
                    <Link to="/admin/logStored"> <MyButton type="success" value="Log stored procedure" /></Link>
                </div>
            )}>

            <Select onChange={handleChangeOption} options={options} />
            <div>
                <pre className="terminal">{dataSource}</pre>
            </div>
        </MyBox>
    );
}
